.banner {
    margin-top: -10vh;
    height: 80vh;
    margin-bottom: -60px;
    color: white;
    object-fit: contain;
}
.banner__contents {
    margin-left: 30px;
    padding-top: 300px;
    /* height: 190px; */
}
.banner__title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner__description {
    width: 45rem;
    line-height: 1;
    padding: 1rem 0;
    font-size: 1rem;
    max-width: 360px;
    height: 80px;
}
.banner__button {
    cursor: pointer;
    color: #fff;
    border: none;
    outline: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 1rem 3rem;
    margin-right: 2rem;
    background-color: rgba(51, 51, 51, 0.5);
}
.play {
    background-color: #fff;
    color: #000;
}
.banner__button:hover {
    background-color: #c6c6c6;
    color: #000;
    transition: all 0.2s;
}
.banner--fadeBottom {
    height: 40vh;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.91),
        #111
    );
}

/* Media query for mobile view */
@media (max-width: 768px) {
    .banner{
      margin-bottom: 50px; /* Add margin between the components */
    }
    .banner__button {
        font-size: 0.9em; /* Adjust the font size */
        padding: 8px 16px; /* Adjust the padding */
      }
  }